// Bigger Style
.Bar {
  // TEMPORARY BORDER
  // border: solid 1px lime;
  
  width: 960px;
  display: flex;
  align-items: center ;
  justify-content: space-between;
  position: absolute;
  top: 0;

}

#nav {
  // border: 1px solid pink;
  width: 400px;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  list-style: none;
}

#nav li {
  font-size: 16px; // less than 18px
  font-weight: 500;
}



